import { Component, OnInit, Output, EventEmitter } from '@angular/core';
@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.sass']
})
export class AboutComponent implements OnInit {

  @Output() triggerAnimationEventEmitter = new EventEmitter<any>();

  constructor() {}

  ngOnInit() {
    this.setComponentHeight();
  }

  /**
   * Adjust height, of the start screen to fill up the whole screen (- navbar)
   */
  setComponentHeight() {
    const header = document.getElementById('navigation');
    const headerHeight = header.offsetHeight;

    const startSection = document.getElementById('about-visible-on-start-container');
    const viewportHeight = window.innerHeight;
    const availableHeight = viewportHeight - headerHeight;

    // console.log('headerheight: ', headerHeight);
    // console.log('viewportHeight: ', window.innerHeight);
    // console.log('availableHeight: ', availableHeight);

    // Section padding is 3 rem and rem is according to browser font size, usually 1rem:16px
    startSection.style.height = (availableHeight - 48).toString() + 'px';

    // const convertRemToPixels = (rem) => {
    //   return rem * parseFloat(getComputedStyle(document.documentElement).fontSize);
    // }
    // console.log('pixels: ', convertRemToPixels(3));

  }

  scrollToBio() {
    const el = document.getElementById('experience');
    scroll({
      top: el.offsetTop - document.documentElement.clientHeight,
      behavior: 'smooth'
    });
    this.triggerAnimationEventEmitter.emit();
  }

}
