import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { Observable } from 'rxjs/internal/Observable';
import { Subscription } from 'rxjs/internal/Subscription';

@Component({
  selector: 'app-experience',
  templateUrl: './experience.component.html',
  styleUrls: ['./experience.component.sass']
})
export class ExperienceComponent implements OnInit {
  /**
   * stores which tab content is displayed
   */
  selectedCategory = 'programming';

  /**
   * array to store data for progress bars
   */
  skillData = {
    programming: [
      {assetURL: '../../assets/svg/HTML5_logo.svg', altText: 'html5-logo', dataLabel: '95%', value: 'value-95'},
      {assetURL: '../../assets/svg/CSS3_logo.svg', altText: 'css3-logo', dataLabel: '65%', value: 'value-65'},
      {assetURL: '../../assets/svg/JavaScript_logo.svg', altText: 'javascript-logo', dataLabel: '85%', value: 'value-85'},
    ],
    frameworks: [
      {assetURL: '../../assets/svg/Angular_full_color_logo.svg', altText: 'angular-logo', dataLabel: '90%', value: 'value-90'},
      {assetURL: '../../assets/svg/React_logo.svg', altText: 'react-logo', dataLabel: '40%', value: 'value-40'},
      {assetURL: '../../assets/svg/Node.js_logo.svg', altText: 'node-js-logo', dataLabel: '65%', value: 'value-65'},
    ],
    devops: [
      {assetURL: '../../assets/svg/Microsoft_Azure_logo.svg', altText: 'microsoft-azure-logo', dataLabel: '75%', value: 'value-75'},
      {assetURL: '../../assets/svg/Amazon_Web_Services_logo.svg', altText: 'amazon-webservices-logo', dataLabel: '20%', value: 'value-20'},
      {assetURL: '../../assets/svg/Docker_logo.svg', altText: 'docker-logo', dataLabel: '35%', value: 'value-35'},
    ]
  };

  constructor() { }

  ngOnInit(): void {}

  /**
   * called on tab selection
   */
  selectCategory = (category: string, event: any) => {

    // remove is-active class from every element
    const tabList = document.getElementById('experience-tab-list');
    tabList.childNodes.forEach((listElement: HTMLElement) => {
      listElement.classList.remove('is-active');
    });

    this.selectedCategory = category;

    // add is-active class to the selected element
    const element = event.path[1];
    element.classList.add('is-active');
  }

}
