import { AfterViewInit, Component, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { appTranslations } from './app.translations';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.sass']
})
export class AppComponent implements OnInit, AfterViewInit {
  elementsToBeAnimated = [];

  /**
   * stores application language
   * defaults to english
   */
  public language = 'en-EN';
  public isDarkMode = false;

  constructor(private translate: TranslateService) {

    /**
     * adjust the viewport when a navigation happens, to account for header size
     */
    window.addEventListener('hashchange', (event) => {
      const hash = window.location.hash.substring(1);
      if (!hash) {
        return;
      }
      const offset = parseInt(localStorage.getItem('headerHeight'), 10);
      const el = document.getElementById(hash);
      const currentOffset = el.offsetTop;
      scroll({
        top: currentOffset - offset,
        behavior: 'smooth'
      });
      this.triggerAnimations();
    });

    this.initializeTranslations();
  }

  getElementsToBeAnimated() {
    // tslint:disable:max-line-length
    this.elementsToBeAnimated = [
      // About Component Elements
      { element: document.getElementById('about-title'), animations: ['animate__animated', 'animate__fadeInLeft', 'animate__delay-1s'] },
      { element: document.getElementById('about-subtitle'), animations: ['animate__animated', 'animate__fadeInRight', 'animate__delay-1s'] },
      { element: document.getElementById('about-subtext'), animations: ['animate__animated', 'animate__fadeInRight'] },
      { element: document.getElementById('profile-image'), animations: ['animate__animated', 'animate__zoomIn', 'animate__delay-1s'] },
      { element: document.getElementById('arrow-down-icon'), animations: ['animate__animated', 'animate__heartBeat', 'animate__delay-1s', 'animate__infinite', 'animate__slower'] },

      // Experience Component Elements
      { element: document.getElementById('experience-title'), animations: ['animate__animated', 'animate__fadeInRight'] },
      { element: document.getElementById('experience-subtitle'), animations: ['animate__animated', 'animate__fadeInLeft'] },
      // { element: document.getElementById('experience-tab-switch'), animations: ['animate__animated', 'animate__fadeInRight'] },
      // { element: document.getElementById('experience-tab-card'), animations: ['animate__animated', 'animate__fadeInUp'] },
      { element: document.getElementById('experience-tab-container'), animations: ['animate__animated', 'animate__fadeInUp'] },

      // Projects Component Elements
      { element: document.getElementById('projects-title'), animations: ['animate__animated', 'animate__fadeInRight'] },
      { element: document.getElementById('projects-subtitle'), animations: ['animate__animated', 'animate__fadeInLeft'] },
      { element: document.getElementById('panel-entity-1'), animations: ['animate__animated', 'animate__fadeInLeft'] },
      { element: document.getElementById('panel-entity-2'), animations: ['animate__animated', 'animate__fadeInUp'] },
      { element: document.getElementById('panel-entity-3'), animations: ['animate__animated', 'animate__fadeInRight'] },

      // Contact Component Elements
      { element: document.getElementById('contact-title'), animations: ['animate__animated', 'animate__fadeInRight'] },
      { element: document.getElementById('contact-subtitle'), animations: ['animate__animated', 'animate__fadeInLeft'] },

      // Footer Component Elements
      { element: document.getElementById('footer-created-text'), animations: ['animate__animated', 'animate__fadeInDown'] },
      { element: document.getElementById('footer-social-icons'), animations: ['animate__animated', 'animate__fadeInUp'] },
    ];

    // console.log('elementsToBeAnimated: ', this.elementsToBeAnimated);
  }

  /**
   * init
   */
  public ngOnInit() {
    // setTimeout(() => {
    //   document.body.style.transform = 'scale(1)';
    // }, 1000);
    // TODO: implement this later
    // gets users preferred mode
    // const isOsDark = window.matchMedia('(prefers-color-scheme: dark)').matches;
    // console.log('isOsDark: ', isOsDark);

    this.initializeDarkModeSwitch();
  }

  public ngAfterViewInit() {
    this.getElementsToBeAnimated();
    this.triggerAnimations();
    this.initializeScrollEventHandler();
  }

  /**
   * checks if given html element is visible inside the current viewport
   */
  isElementInViewport(el) {
    const rect = el.getBoundingClientRect();
    return (
      rect.top >= 0 &&
      rect.left >= 0 &&
      rect.bottom <= (window.innerHeight || document.documentElement.clientHeight) && /* or $(window).height() */
      rect.right <= (window.innerWidth || document.documentElement.clientWidth) /* or $(window).width() */
    );
  }

  initializeScrollEventHandler() {
    window.addEventListener('scroll', (event) => {
      requestAnimationFrame(() => {
        this.triggerAnimations();
      });
    }, { passive: true });
  }

  triggerAnimations() {
    const animationfunction = () => {
      this.elementsToBeAnimated.forEach((element) => {
        if (window.getComputedStyle(element.element).visibility === 'hidden') {
          if (this.isElementInViewport(element.element)) {
            this.addAnimationClasses(element);
          }
        }
      });
    };
    animationfunction();
  }

  /**
   * adds animation classes to the template elements
   */
  addAnimationClasses(el: { element: any, animations: string[] }) {
    el.element.parentElement.style.overflow = 'hidden';
    el.animations.forEach((animation) => {
      el.element.classList.add(animation);
    });
    el.element.style.visibility = 'visible';
  }

  /**
   * Initialize the Darkmode Switch button event listener
   */
  initializeDarkModeSwitch() {
    document.getElementById('darkmode-togglebutton').addEventListener('click', () => {
      if (!this.isDarkMode) {
        document.body.classList.replace('light', 'dark');
      } else {
        document.body.classList.replace('dark', 'light');
      }
      this.isDarkMode = !this.isDarkMode;
    });
  }

  /**
   * Initializes Translations
   */
  initializeTranslations() {
    // get users browser language
    const browserLang = navigator.language;
    // initialize translations
    this.translate.setTranslation('en-EN', appTranslations.en);
    this.translate.setTranslation('de-DE', appTranslations.de);

    // set language according to browser language
    if (browserLang === 'de-DE') {
      this.translate.setDefaultLang('de-DE');
      this.language = 'de-DE';
    } else if (browserLang === ('en-EN' || 'en-GB')) {
      this.translate.setDefaultLang('en-EN');
      this.language = 'en-EN';
    } else {
      this.translate.setDefaultLang('en-EN');
      this.language = 'en-EN';
    }
    this.translate.use(this.language);
  }
}
