import { Component, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.sass']
})
export class HeaderComponent implements OnInit {

  public language = 'en-EN';
  public heading = 'About me';
  private headingKey = 'HEADER.ABOUT';
  private darkMode = false;
  public darkModeButtonKey = 'HEADER.DARKMODE';

  constructor(private translate: TranslateService) {
    window.onscroll = () => {
      this.setScrollIndicator();
    };
  }

  ngOnInit() {
    this.addAnimationClasses();
    this.setMarginToContent();
  }

  setScrollIndicator() {
    const winScroll = document.body.scrollTop || document.documentElement.scrollTop;
    const height = document.documentElement.scrollHeight - document.documentElement.clientHeight;
    const scrolled = (winScroll / height) * 100;
    document.getElementById('scroll-indicator-bar').style.width = scrolled + '%';
  }


  setMarginToContent(): void {
    const headerHeight = document.getElementById('header-brand').clientHeight;
    localStorage.setItem('headerHeight', headerHeight.toString());
    const heightString = headerHeight.toString() + 'px';
    const headerSection = document.getElementById('header-section');
    headerSection.style.marginBottom = heightString;
  }

  addAnimationClasses() {
    const innerNav = document.getElementById('inner-navigation');
    innerNav.parentElement.style.overflow = 'hidden';
    innerNav.classList.add('animate__animated', 'animate__fadeInDown');
  }

  toggleDarkMode() {

    const buttonImage = document.getElementById('darkmode-button-image') as HTMLImageElement;

    if (!this.darkMode) {
      buttonImage.src = '../../../assets/icons/sun.svg';
      this.darkModeButtonKey = 'HEADER.LIGHTMODE';
    } else {
      buttonImage.src = '../../../assets/icons/moon.svg';
      this.darkModeButtonKey = 'HEADER.DARKMODE';
    }

    this.darkMode = !this.darkMode;
    // console.log('darkMode: ', this.darkMode);
  }

  // changes the applications language
  changeLanguage(lang: string) {
    this.language = lang;
    this.translate.use(this.language);
    this.heading = this.translate.instant(this.headingKey);
  }

  // changes the navbars heading
  assignHeading(heading: string, section: string) {
    this.headingKey = heading;
    this.heading = this.translate.instant(this.headingKey);
    this.navigateToSection(section);
  }

  navigateToSection(section: string) {

    window.location.hash = section;

    // TODO: we might need this later for the CV presentation feature
    // if (section.length > 0) {
    //   if (this.router.url === '/cv') {
    //     this.router.navigateByUrl('/abouÂ´').then(() => {
    //       // window.location.href = '/home#' + section;
    //       // window.location.hash = '';
    //       window.location.hash = section;
    //     });
    //   } else {
    //     // window.location.href = '/home#' + section;
    //     // window.location.hash = '';
    //     window.location.hash = section;
    //   }
    // }


    // close the menu if open
    const burger = document.getElementById('burgerMenu');
    burger.click();

  }

  toggleActive() {
    const $burger = document.getElementById('burgerMenu');
    const $target = document.getElementById($burger.dataset.target);

    if ($burger.classList.contains('is-active')) {
      $burger.classList.remove('is-active');
    } else {
      $burger.classList.add('is-active');
    }

    if ($target.classList.contains('is-active')) {
      $target.classList.remove('is-active');
    } else {
      $target.classList.add('is-active');
    }
  }

}
